import React from 'react';
import { CDBSidebarFooter, CDBBox, CDBBtn, CDBIcon } from 'cdbreact';
import '../App.css';

function Footer () {
  return (
    <CDBSidebarFooter className="shadow">
    <CDBBox
      display="flex"
      justifyContent="between"
      alignItems="center"
      className="mx-auto py-4 flex-wrap"
      style={{ width: '80%' }}
    >
      <CDBBox display="flex" alignItems="center">
        <a href="/" className="d-flex align-items-center p-0 text-dark">
          <img
            alt="logo"
            src="data:image/png;base64,"
            width="30px"
          />
          <span className="ms-4 h5 mb-0 font-weight-bold">FinaincialWise</span>
        </a>
      </CDBBox>
      <CDBBox>
        <small className="ms-2">&copy; FinaincialWise, 2023. All rights reserved.</small>
      </CDBBox>
      <CDBBox display="flex">
        <CDBBtn flat color="red" id='social-btn' className="p-2">
          <CDBIcon fab icon="facebook-f" />
        </CDBBtn>
        <CDBBtn flat color="red" id='social-btn' className="mx-3 p-2">
          <CDBIcon fab icon="twitter" />
        </CDBBtn>
        <CDBBtn flat color="red" id='social-btn'className="p-2">
          <CDBIcon fab icon="instagram" />
        </CDBBtn>
      </CDBBox>
    </CDBBox>
  </CDBSidebarFooter>
  );
};
export default Footer;